import nome from '../img/nomeAbout.png'
import autor from '../img/autor-idealizador.png'
import perfil from '../img/perfil.png'
import insta from '../img/insta.png'
import gmail from '../img/gmail.png'
import barraSobre from '../img/barraSobre.png'

const Sobre = () => {


    return (
        <>
            <div className="containerSobre">
                <div className='titulosSobre'>

                    <img className='autor' src={autor} />
                    <img className='nome' src={nome} />
                    <img className='perfil' src={perfil} />
                </div>
                <div className='redesSociais'>
                    <a href='https://www.instagram.com/tribos_ancestrais?igsh=MWwybzNtZ3dyd2I5dQ==' target='blank'>

                        <img className='insta' src={insta} />
                    </a>
                    <img className='gmail' src={gmail} />
                </div>
            </div>
            <div className='containerSobreTexto'>

                <img className='barraSobre' src={barraSobre} />
                <div className='textoSobre'>
                    <div className='textoSobre-2'>

                        <p>Ol! Meu nome  Andrei, e vou contar aqui como me tornei apaixonado por histrias! Tudo comeou com os desenhos animados que assistia todos os dias e as idas ao cinema com meus pais. Cresci influenciado pelo Goku de DBZ e por inmeros filmes de Hollywood. Hoje, sou um consumidor de histrias de todos os tipos, como livros, mangs, sries e filmes. Histrias sempre me ajudaram a entender melhor o mundo, e por isso as admiro. </p>
                        <p>Nunca me canso de assistir a um personagem brilhando na tela depois de ter batalhado tanto.  sempre inspirador absorver uma boa histria.</p>
                        <p>Atualmente, estudo para me tornar programador e, nas horas vagas, me dedico a escrever minha primeira narrativa, Tribos Ancestrais. A histria nasceu de muitas caminhadas pelas ruas com o fone de ouvido no mximo e aquela boa msica de ao. No sei ao certo quando comecei a querer escrever, e mesmo que soubesse, no incio, minhas ideias eram apenas personagens malucos pulando por a. Comecei a escrever os primeiros captulos de Tribos Ancestrais durante a pandemia, enquanto fazia alguns cursos de criao literria, juntamente com muitos vdeos do YouTube. Todo esse processo sempre foi divertido para mim, e, apesar das dificuldades,  isso que me move at hoje!  </p>
                        <p><b>Bom...  isso. Espero que gostem de "Tribos Ancestrais"!</b></p>
                    </div>
                </div>
            </div>

        </>
    )
}

export default Sobre;